@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{background-color:#f5f5f5}.app *{font-family:"Poppins",sans-serif;font-weight:500}.app .greeting{text-align:center}.app .greeting__heading{color:#000;font-family:"Poppins",sans-serif;font-weight:800;font-size:3rem}.app .greeting>*{margin:20px}.app .wrapper{display:flex;justify-content:center;flex-wrap:wrap;grid-gap:3rem;gap:3rem}
.counter{display:flex;flex-direction:column;align-items:center;width:90%;height:55%;border:4px solid #fff;border-radius:15px}.counter__title{font-size:1.5rem;font-weight:700}.counter__score{font-size:3rem;font-weight:700;color:#fff;margin:0}.counter .button-container{width:80%;display:flex;justify-content:space-between}.counter .button-container .button{display:flex;justify-content:center;align-items:center;cursor:pointer;background-color:#fff;width:50px;height:50px;border-radius:50%;border:2px solid #000;font-size:1.5rem;color:#000}
.employee{display:flex;flex-direction:column;align-items:center;border:1px solid #000;width:300px;height:400px;border-radius:15px;background:linear-gradient(10deg, #FEC163, #DE4313);border-radius:15px;box-shadow:rgba(0,0,0,.16) 0px 3px 6px,rgba(0,0,0,.23) 0px 3px 6px}.employee__name{font-family:"Poppins",sans-serif;font-weight:700;font-size:1.5rem;color:#fff;border-bottom:2px solid #000}.employee__role{font-size:1.3rem;font-weight:300}
